<template>
  <v-app id="inspire">
    <v-navigation-drawer v-model="drawer" app>
      <!--  --><v-list>
        <v-list-item-group v-model="model">
          <v-list-item v-for="(item, i) in items" :key="i" @click="link(item)">
            <v-list-item-title>{{ item }}</v-list-item-title>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>

    <v-app-bar app shrink-on-scroll>
      <v-app-bar-nav-icon @click="drawer = !drawer"></v-app-bar-nav-icon>

      <v-toolbar-title>Main</v-toolbar-title>
    </v-app-bar>

    <v-main>
      <!--  -->
      <pdf ref="pdf" v-for="i in numPages" :key="i" :src="url" :page="i"></pdf>
    </v-main>
  </v-app>
</template>

<script>
import pdf from 'vue-pdf'

export default {
  name: 'App',
  components: {
    pdf
  },
  data: () => ({
    url: 'https://cdn.jsdelivr.net/gh/123liusong/imgs@1.1/ch1极限、连续与间断.pdf',
    numPages: null, // pdf 总页数
    drawer: false,
    items: [
      'ch1极限、连续与间断',
      'ch2导数计算及应用',
      'ch3不定积分',
      'ch4定积分',
      'ch5常微分方程'
    ]
  }),
  methods: {
    getNumPages () {
      let loadingTask = pdf.createLoadingTask(this.url)
      loadingTask.promise
        .then(pdf => {
          this.numPages = pdf.numPages
        })
        .catch(err => {
          console.error('pdf 加载失败', err)
        })
    },
    link (item) {
      this.url =
        'https://cdn.jsdelivr.net/gh/123liusong/imgs@1.1/' + item + '.pdf'
      alert(this.url)
    }
  },
  mounted () {
    this.getNumPages()
  }
}
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



